import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export const constantRouterMap = [
  {
    path: '/',
    component: () => import('@/components/Login/login'),
    hidden: true
  },
  {
    path: '/home',
    component: () => import('@/components/home/home'),
    hidden: true,
    children: [
      {
        path: '/newreferral',
        component: () => import('@/components/content/newReferral')
      },
      {
        path: '/referralrecord',
        component: () => import('@/components/content/referralRecord')
      },
      {
        path: '/referraltotal',
        component: () => import('@/components/content/referralTotal')
      },
      {
        path: '/referraltrend',
        component: () => import('@/components/content/referralTrend')
      },
      {
        path: '/inhospitalcost',
        component: () => import('@/components/content/inhospitalCost')
      }
    ]
  }
]

export default new Router({
  mode: 'history', // 后端支持可开
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
})



